import React from "react";
import "./../css/components.css";
import { useState, useEffect } from "react";
function Header(props) {
  const [tab, settab] = useState("home");

  useEffect(() => {
    if (tab) {
     var elements = document.getElementsByClassName("headerbutton");
      for (var i = 0; i < elements.length;i++){
        elements[i].style.backgroundColor = "white";
        elements[i].style.color = "black";
        
      }
      
      document.querySelector("#" + tab).style.backgroundColor = "black";
      document.querySelector("#" + tab).style.color = "white";
      }
    
  }, [tab])

  const onclick = (e) => {
    props.settab(e.target.innerHTML);
    settab(e.target.innerHTML);
  };
  return (
    <>
      {" "}
      <div className="headertitle">
        <h1 className="Reacttimes">The React Times</h1>
        <p style={{ fontFamily: "Grenze Gotisch" }}>
          Latest NEWS displayed with the latest Tech
        </p>
      </div>
      <div className="headercontainer">
        <li className="headerul">
          <ul>
            <button id="arts" className="headerbutton" onClick={onclick}>
              arts
            </button>
          </ul>
          <ul>
            <button id="automobiles" className="headerbutton" onClick={onclick}>
              automobiles
            </button>
          </ul>

          <ul>
            <button id="business" className="headerbutton" onClick={onclick}>
              business
            </button>
          </ul>
          <ul>
            <button id="Fashion" className="headerbutton" onClick={onclick}>
              Fashion
            </button>
          </ul>
          <ul>
            <button id="Food" className="headerbutton" onClick={onclick}>
              Food
            </button>
          </ul>

          <ul>
            <button id="home" className="headerbutton" onClick={onclick}>
              home
            </button>
          </ul>
          <ul>
            <button id="Insider" className="headerbutton" onClick={onclick}>
              Insider
            </button>
          </ul>

          <ul>
            <button id="Magazine" className="headerbutton" onClick={onclick}>
              Magazine
            </button>
          </ul>
          <ul>
            <button id="Movies" className="headerbutton" onClick={onclick}>
              Movies
            </button>
          </ul>
          <ul>
            <button id="technology" className="headerbutton" onClick={onclick}>
              technology
            </button>
          </ul>
        </li>
      </div>
    </>
  );
}

export default Header;
